.body {
    margin : 0;
    padding : 0; /* 마진과 패딩의 여백을 없애줌 */
}

.sort {
    display: flex;
}

.imgs {
    width: 100px;
    height: 100px;
}

table td {
    padding: 12px;
    font-size: 12px;
}

table th {
    padding: 12px;
    font-size: 12px;
    font-weight: 110;
}

.wishbutton {
    background: white;
    padding: 8px 18px;
    margin-right: 2px;
    margin-left: 2px;
    border-color: gray gray;
    font-size: 13px;
    height: 100%;
}


.cart {
    width: 70%;
    margin: 0 auto;
}

table {
    width: 100%;
}

table , th, td {
    border-left: none;
    border-right: none;
    border-collapse: collapse;
}

table td {
    /*border-top: none;*/
    border-bottom: none;
}

.paytable table, .paytable th, .paytable td {
    border-left: none;
    border-right: none;
    border-collapse: collapse;
}


.paytable tr:nth-child(2) td:nth-child(3) {
    text-align: center;
}

.paytable2 {
    border-top: hidden;
}

.payway {
    display: flex;
    flex-direction: row;
}

.paywaygrid {
    display: grid;
    grid-template-columns: repeat(2,1fr);
}

.paywaygrid div:nth-child(2) {
    text-align: right;
}

.paywaygrid div:nth-child(2) span {
    margin-left: 50%;
}

.paywaygrid p {
    font-size: 12px;
}

.paywaygrid div:nth-child(1) span {
    font-size: 12px;
}

.paywaybtn {
    width: 100%;
    padding: 20px;
    background-color: black;
    color: white;
    font-size: 20px;
    margin-top: 25px;
    margin-bottom: 10%;
}

.paywaygrid h2 {
    margin-left: 20%;
}

.arrowbutton {
    display: flex;
    flex-direction: column;
}

.arrow:hover {
    cursor: pointer;
}


.quantity {
    display: flex;
    justify-content: right;
}

.quantity span {
    align-self: center;
    margin-right: 4px;
}

.selected {
    font-size: 12px;
}



.paytable2 tr:nth-child(1) td:nth-child(1) {
    text-align: left;
    border-right: 1px solid gray;
    border-bottom: 1px solid gray;
    width: 120px;
}

.paytable2 tr:nth-child(1) td:nth-child(2) {
    text-align: left;
    border-bottom: 1px solid gray;
}

.paytable2 tr:nth-child(2) td:nth-child(2) {
    text-align: left;
    border-bottom: 1px solid gray;
}

.paytable2 tr:nth-child(2) td:nth-child(1) {
    text-align: left;
    border-right: 1px solid gray;
}

th {
    border-top: gray;
}

td{
    text-align: center;
}

td:nth-child(3) {
    text-align: left;
}

.tfoots {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
}

.tfoots2 {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    margin-bottom: 20%;
}

.arrow {
    border: none;
}


tfoot td {
    text-align: right;
    font-size: 12px;
    padding: 20px;
}

.secondtable th, .secondtable td {
    padding: 24px;
}

.secondtable td:nth-child(3) {
    text-align: center;
}

.secondtable th {
    font-size: 10px;
}

.secondtable td {
    
    font-size : 18px;
}

.order {
    display: flex;
    padding: 24px;
    justify-content: center;
}

.order button {
    margin : 2px;
    padding: 10px;
}

.order button:nth-child(1) {
    background: black;
    color: white;
}

.textlink {
    color: inherit;
    text-decoration: inherit;
}

@media screen and (max-width : 500px) {

    .cart {
        width: 96%;
        margin: 0 auto;
    }



    
}

@media screen and (max-width : 840px) {
    .wishbutton {
        background: white;
        padding: 4px 8px;
        margin-right: 2px;
        margin-left: 2px;
        border-color: gray gray;
        font-size: 7px;
    }

    .selected {
        font-size: 10px;
        font-weight: bold;
    }

    .mobilecart {
        display: flex;
        flex-direction: column;
        align-items: start;
        margin-left: 2px;
    }

    .mobileorder button{
        width: 100%;
        padding: 10px 4px;
        border-radius: 4px;
        color: white;
        background-color: black;
    }

    .mobileorder {
        margin-bottom: 30%;
        margin-top: 10%;
    }

    .mobileremove {
        width: 50%;
    }

    .paytable tr:nth-child(1) th:nth-child(1) {
        width: 1000px
    }

    table tr:nth-child(1) td:nth-child(1) {
        width: 50px;
    }

    table tr:nth-child(1) td:nth-child(3) {
        width: 100px;
    }

    table td {
        border-top: none;
        border-bottom: none;
    }
    
}