.container {
    margin-left: 22%;
    margin-right: 3%;
    margin-top: 5%;
    margin-bottom: 5%;
}

.review {
    display: flex;
    justify-content: space-between;
}

.buttons {
    align-self: center;
}

.buttons button:nth-child(1) {
    margin-right : 8px;
    color: white;
    background-color: black;
    padding : 4px 12px;
    font-size: 12px;
}

.buttons button:nth-child(2) {
    padding: 4px 12px;
    font-size: 12px;
    background-color: white;
}

.tables {
    width: 100%;
}

.tables tr:nth-child(1) th:nth-child(1) {
    width: 20px;
}

.tables tr:nth-child(1) th:nth-child(2) {
    width: 600px;
}

.tables tr:nth-child(1) th:nth-child(3) {
    width: 120px;
}

.tables tr:nth-child(1) th:nth-child(4) {
    width: 70px;
}

.tables tr:nth-child(1) th:nth-child(5) {
    width: 50px;
}

.tables td:nth-child(3) {
    text-align: center;
}

.tables td {
    padding: 20px;
}

.write {
    margin-top: 5%;
    display: flex;
    flex-direction: column;
    text-align: end;
}

.write span {
    margin-right: 20px;
}

.write input {
    width: 250px;
    border-radius: 4px 4px;
}

.write textarea {
    width: 250px;
    height: 100px;
    border-radius: 4px;
}

.spans {
    vertical-align: top;
}

.spans1 {
    display: block;
    text-align: center;
    font-size: 12px;
    color: gray;
}

.pagenation {
    margin-top: 10%;
    text-align: center;
}

.reviewcontainer {
    display: grid;
    grid-template-columns: repeat(5,1fr);
    width: 100%;
    font-size: 12px;
    text-align: center;
}

.reviewcontainer span {
    padding: 7px 4px;
}

.reviewcontainer span:nth-child(2) {
    width: 500px;
}

.reviewbutton {
    font-size: 12px;
    background-color: black;
    color: white;
    margin-right: 4px;
}

.contents {
    width: 100%;
    margin-top: 2%;
    margin-left: 6%;
    font-size: 12px;
}

.textlink {
    text-decoration: inherit;
    color: inherit;
}




@media screen and (max-width : 500px) {
    .container {
        margin-left: 2%;
        margin-right: 2%;
        margin-top: 20%;
        margin-bottom: 5%;
    }

    .reviewcontainer {
        display: grid;
        grid-template-columns: repeat(5,1fr);
        width: 100%;
        font-size: 12px;
        text-align: center;
    }
    
    .reviewcontainer span {
        padding: 7px 4px;
    }
    
    .reviewcontainer span:nth-child(2) {
        width: 100px;
    }

    .reviewbutton {
        font-size: 12px;
        background-color: black;
        color: white;
        margin-bottom: 4px;
    }


    
    
}