.body {
    margin : 0;
    padding : 0; /* 마진과 패딩의 여백을 없애줌 */
}

.textareas {
    width: 440px;
    height: 160px;
    resize: none;
    white-space: pre-line;
}

.sort {
    display: flex;
}

.submits {
    background-color: black;
    color: white;
    padding: 8px 30px;
    font-size: 12px;
}

.phone input:nth-child(2), .phone input:nth-child(3) {
    width: 50px;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid gray;

}

.phone select:nth-child(1) {
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid gray;
}

.loginform {
    margin-top: 5%;
    margin-bottom: 30%;
}


.mgbt {
    margin-top: 18%;
}

.mgbt input:nth-child(1) {
    width: 400px;
    border-top: none;
    border-right: none;
    border-left: none;
    border-bottom: 1px solid gray;
}


.flexcontainer {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    column-gap: 50px;
    margin-left: 15%;
    width: 100%;
}


.footer {
    bottom: 1px;
}

.container {
    margin : 0 auto;
}

.logincontainer {
    display: flex;
    flex-direction: column;
    margin-right: 200px;
}

.logincontainer h2 {
    text-align: center;
    margin-top: 10%;
}

.spans {
    text-align: center;
    margin-bottom: 25%;
    margin-right: 200px;
}



.logincontainer input:nth-child(2) {
    width: 400px;
    height: 40px;
    margin-top: 10%;
}

.logincontainer input:nth-child(3) {
    width: 400px;
    height: 40px;
}

.logincontainer input:nth-child(4) {
    width: 409px;
    height: 40px;
}

@media screen and (max-width : 500px) {
    .logincontainer input:nth-child(2) {
        width: 260px;
        height: 40px;
    }
    
    .logincontainer input:nth-child(3) {
        width: 260px;
        height: 40px;
    }
    
    .logincontainer input:nth-child(4) {
        width: 268px;
        height: 40px;
    }

    .logincontainer h2 {
        width: 260px;
        text-align: center;
        margin-left: 10px;
    }

    .logincontainer {
        display: flex;
        flex-direction: column;
    }

    .spans {
        text-align: center;
        width: 200px;
    }

    .sort {
        display: block;
    }

    .container {
        margin-left: 18%;
    }

    .flexcontainer {
        display: grid;
        grid-template-columns: repeat(1,1fr);
        column-gap: 50px;
        margin-left : 6%;
        margin-right: 0%;
        width: 94%;
    }

    .submits {
        background-color: black;
        color: white;
        padding: 8px 30px;
        font-size: 12px;
        margin-bottom: 10%;
    }

    .textareas {
        width: 340px;
        height: 160px;
        resize: none;
        white-space: pre-line;
    }

    .mgbt input:nth-child(1) {
        width: 340px;
        border-top: none;
        border-right: none;
        border-left: none;
        border-bottom: 1px solid gray;
    }
    
}